
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Scroll from "components/scroll";
import { mapGetters } from "vuex";

export default {
beforeRouteLeave(to, fromb, next) {
this.$vux.loading.hide();
next();
},
mounted() {
this.$bus.on("reOpenMap", () => {
setTimeout(() => {
this.openMap();
}, 200);
});
this.lat = this.$route.query.lat || "";
this.lng = this.$route.query.lng || "";
this.openMap();

let w = document.body.clientWidth;
let h = document.body.clientHeight;
this.scrollW = w;
let headerH = 48;
let mapH = 280;
this.scrollH = h - mapH - headerH - 76;
if (!this.scrollH || this.scrollH < 0) {
this.scrollH = 350;
}
},
data() {
return {
geolocation: "",
geoc: "",
poiList: [],
scrollW: 0,
scrollH: 0,
map: {},
mk: "",
point: "",
market: false,
lat: "",
lng: "",
localSearch: "",
localSearchResult: "",
mapLabel: "",
local: "",
myKeys: ["产业园", "路", "厦"],
initPoint: []
};
},
computed: {
...mapGetters(["getCustomerArea"])
},
methods: {
back() {
this.$vux.loading.hide();
this.$router.back();
},
inital() {
this.map.centerAndZoom(this.initPoint, 15);
},
openMap() {
this.$vux.loading.show({
text: "地图加载中..."
});
if (!BMap) {
this.$vux.loading.hide();
this.$vux.toast.show({
text: "地图加载失败"
});
}

let map = new BMap.Map("container");
let geolocation = new BMap.Geolocation();
let geoc = new BMap.Geocoder();

this.map = map;
this.geolocation = geolocation;
this.geoc = geoc;


let _this = this;


if (this.lat && this.lng) {
this.$vux.loading.hide();
let point = new BMap.Point(this.lng, this.lat);
this.initPoint = point;
if (point) {
map.centerAndZoom(point, 15);
_this.market = true;
map.panTo(point);

_this.showLabelAndLocal(point);
}
} else if (this.getCustomerArea.province !== "") {
this.$vux.loading.hide();
geoc.getPoint(
this.getCustomerArea.province +
this.getCustomerArea.city +
this.getCustomerArea.area,
function(point) {
_this.point = point;
_this.initPoint = point;
if (point) {
map.centerAndZoom(point, 15);
_this.market = true;
map.panTo(point);

_this.showLabelAndLocal(point);
}
}
);
} else {

this.$vux.loading.hide();
geolocation.getCurrentPosition(
function(r) {
_this.point = r.point;
_this.initPoint = r.point;
if (this.getStatus() === BMAP_STATUS_SUCCESS) {
map.centerAndZoom(r.point, 15);
map.panTo(r.point);
_this.market = true;
_this.showLabelAndLocal(r.point);
} else {
alert("failed" + this.getStatus());
}
},
{ enableHighAccuracy: true }
);
}

map.addEventListener("moveend", function() {
let point = map.getCenter();
_this.$refs.input.blur();
_this.showLabelAndLocal(point);
});
},
selectAddress(item) {
this.$bus.emit("selectAddCustomerAddress", {
point: item.point,
title: item.title,
address: item.address,
city: item.city,
province: item.province,
district: item.district ? item.district : "",
seleType: this.$route.query.seleAdressType
});
this.$router.back();
},
selectCurrentPoint() {
let _this = this;
let point = this.map.getCenter();
_this.geoc.getLocation(point, function(res) {
let item = {};
item.point = res.point;
item.title = "";
item.address = res.address;
item.province = res.addressComponents.province;
item.city = res.addressComponents.city;
item.district = res.addressComponents.district;
_this.selectAddress(item);
});
},
selectNearbyAddress(nearby) {
let _this = this;
let point = nearby.point;
_this.geoc.getLocation(point, function(res) {
let item = {};
item.point = res.point;
item.title = nearby.title;
item.address = nearby.address;
item.province = res.addressComponents.province;
item.city = res.addressComponents.city;
item.district = res.addressComponents.district;
_this.selectAddress(item);
});
},
showLabelAndLocal(point) {
let _this = this;
this.geoc.getLocation(point, function(rs) {
if (!_this.mapLabel) {
_this.mapLabel = new BMap.Label(rs.address, {
position: point
});
_this.map.addOverlay(_this.mapLabel);
} else {
_this.mapLabel.setContent(rs.address);
}
_this.mapLabel.setPosition(point);

if (rs.surroundingPois && rs.surroundingPois.length > 0) {
_this.poiList = rs.surroundingPois;
} else {
_this.poiList = [];
}
});
},

gotoSearch() {
let _this = this;

var local = new BMap.LocalSearch(this.map, {

onSearchComplete: function(result) {
if (result.Ar.length > 0) {
let _ar = result.Ar;
_this.poiList = _ar;
_this.map.centerAndZoom(_this.poiList[0].point, 16);







} else {
_this.poiList = [];
}
}
});
if (this.localSearch) {
local.search(this.localSearch);
} else {
this.$vux.toast.show({
text: "请输入要搜索的地址",
position: "middle",
type: "warn"
});
return;
}
},
setMapLocation(lng, lat) {
let point = new BMap.Point(lng, lat);
this.map.centerAndZoom(point, 15);
this.map.panTo(point);
this.market = true;
this.showLabelAndLocal(point);
},
geoLngLatToBaidu(lng, lat, coordtype) {
let baiduAK = "W6WIQmkWMM7fHZ1bQzONDGwimUikfaPU";
let from = coordtype == "gcj02" ? 3 : 1;
let url =
"http://api.map.baidu.com/geoconv/v1/?coords=" +
lng +
"," +
lat +
"&output=json&from=" +
from +
"&to=5&ak=" +
baiduAK;
return new Promise((resolve, reject) => {
jsonp(url, null, (err, res) => {
if (err) {
console.error(err.message);
} else {
if (res && res.status == 0) {
resolve({
lng: res.result[0].x,
lat: res.result[0].y
});
}
}
});
});
},
getLocation() {
let _this = this;
let location = { coords: {}, address: {} };
if (plus.os.name == "iOS") {
plus.geolocation.getCurrentPosition(
res => {
_this
.geoLngLatToBaidu(
res.coords.longitude,
res.coords.latitude,
res.coordsType
)
.then(_res => {
location.coords.longitude = _res.lng;
location.coords.latitude = _res.lat;
this.setMapLocation(
location.coords.longitude,
location.coords.latitude
);
});
},
function(e) {
alert("定位失败，请检查网络");
console.log(JSON.stringify(e));
}
);
} else {
plus.geolocation.getCurrentPosition(
res => {
res.address.address = res.address.street;
this.setMapLocation(res.coords.longitude, res.coords.latitude);
},
function(e) {
alert("定位失败，请检查网络");
console.log(JSON.stringify(e));
},
{
provider: "baidu",
geocode: true,
coordsType: "bd09ll"
}
);
}
}
},
components: {
XHeader,
Group,
Cell,
Scroll,
Search,
XInput,
Icon,
XButton
}
};



